import React, {useState,useEffect} from 'react'
import { makeStyles } from '@material-ui/core/styles';
import Divider from '@material-ui/core/Divider';
import './MyOrders.css'
import { Link } from 'react-router-dom';
import Button from '@material-ui/core/Button';
import TodayIcon from '@material-ui/icons/Today';
import Typography from '@material-ui/core/Typography';
import LocalShippingIcon from '@material-ui/icons/LocalShipping';
import ClassTwoToneIcon from '@material-ui/icons/ClassTwoTone';
import Spinner from '../UI/Spinner';
import axios from 'axios';

const useStyles = makeStyles((theme) => ({
    '@keyframes blinker': {
        from: {opacity: 1},
        to: {opacity: 0.2 , color:'#3f51b5',fontWeight:'400'}
    },
    button: {
        marginBottom: theme.spacing(1),
    },
    allocationButton:{
        animationName: '$blinker',
        animationDuration: '0.7s',
        animationTimingFunction: 'linear',
        animationIterationCount:'infinite',
        marginBottom: theme.spacing(1),
        marginRight: theme.spacing(2)
    },
    title: {
        fontSize: 20,
        height: 50,
        padding: 10,
        paddingLeft: 55,
        color: 'white'
    },
  }));
  

const MyOrders=()=>{
    const classes=useStyles();
    const [activeOrders,setActiveOrders] = useState([]);
    const [loading,setLoading]=useState(false);
    useEffect(()=>{

        setLoading(true);

        const url='https://2n3n7swm8f.execute-api.ap-south-1.amazonaws.com/draft0/customerorder/customer/gaurav@gmail.com';
        console.log(url);
        axios.get(url)
        .then(resp=>{
            console.log(resp.data);
            setLoading(false);
            setActiveOrders(resp.data);
        })
        .catch(err=>{
            console.log(err);
        })
        const someOrders = [
            {
                orderId: 'd52de5b9-0099-4623-b7c7-99369ea58cb6',
                orderDate: '12th may 2020',
                status: 'TRACK'
            },
            {
                orderId: '145320b7-c562-42ea-b612-9c519ad739f8',
                orderDate: '12th June 2020',
                status: 'TRACK'
            },
            {
                orderId: '77455960-a39c-4cc0-93d7-ac12eee984a0',
                orderDate: '12th July 2020',
                status: 'TRACK'
            },
            {
                orderId: '2783935c-d4b2-4a91-aa91-9f0c45793515',
                orderDate: '12th Aug 2020',
                status: 'TRACK'
            },{
                orderId: 'd52de5b9-0099-4623-b7c7-99369ea58cb6',
                orderDate: '12th Sep 2020',
                status: 'TRACK'
            },
        ]
        setActiveOrders(someOrders);
    },[]);
    

    let orderList=
    <section className="root">
    {
        activeOrders.map((eachOrder) => (
            <div>
        <figure>
            <img src="data:image/svg+xml;base64," />
            <figcaption>
                <h6>Order Id <span><TodayIcon/></span></h6>
                <h4>{eachOrder.OrderId}</h4>
            </figcaption>
        </figure>
        <div>
            <Button
                    component={Link} to="/track"
                    variant="contained"
                    color="default"
                    className={classes.allocationButton}
                    startIcon={<LocalShippingIcon />}>
                    Track 
            </Button>
            <Button
                    component={Link} to={`order/${eachOrder.OrderId}`}
                    variant="contained"
                    color="default"
                    className={classes.allocationButton}
                    startIcon={<ClassTwoToneIcon />}> 
                    Details
            </Button>
        </div>
        <Divider/>
    </div>
        ))
    }
    </section>

    if(loading==true)
    {
        orderList=
            <div style={{marginTop:"100px"}}>
                <Spinner/>
            </div>
    }
   
    return(
                <div>
                    <Typography className={classes.title} gutterBottom style={{ backgroundColor: '#66bb6a' }}>
                                    My Active Orders
                    </Typography>  

                    {orderList}
                </div>
    )
}



export default MyOrders
